import {
  Button,
  Col,
  DatePicker,
  Divider,
  InputNumber,
  Row,
  Space,
  Tag,
  Typography,
} from "antd";
import React, { useState } from "react";
import { Dayjs } from "dayjs";
import { TimePicker, TimePickerProps } from "./TimePicker";

export const AddCmp = () => {
  const [date, setDate] = useState<Dayjs | null>(null);
  const [time, setTime] = useState<TimePickerProps["value"] | undefined>(
    undefined
  );

  const [day, setDay] = useState<number | null>(null);
  const [hour, setHour] = useState<number | null>(null);
  const [minute, setMinute] = useState<number | null>(null);

  const [calcValue, setCalcValue] = useState<string | undefined>();

  const selectedDate = (() => {
    const newDate = date
      ?.set("h", Number(time?.[0]) ?? 0)
      .set("m", Number(time?.[1]) ?? 0);
    return newDate;
  })();

  const onSubstracts = () => {
    const res = selectedDate
      ?.subtract(day ?? 0, "day")
      .subtract(hour ?? 0, "hour")
      .subtract(minute ?? 0, "minute")
      .format("DD-MM-YYYY HH:mm");
    setCalcValue(res);
  };

  const onAdd = () => {
    const res = selectedDate
      ?.add(day ?? 0, "day")
      .add(hour ?? 0, "hour")
      .add(minute ?? 0, "minute")
      .format("DD-MM-YYYY HH:mm");
    setCalcValue(res);
  };

  const onReset = () => {
    setDate(null);
    setTime(undefined);
    setDay(null);
    setHour(null);
    setMinute(null);
    setCalcValue(undefined);
  };

  return (
    <Row gutter={[16, 16]} style={{ maxWidth: 333 }}>
      <Col span={12}>
        <Space size={4} direction="vertical">
          <Typography.Text> </Typography.Text>
          <DatePicker
            value={date}
            onChange={setDate}
            format={"DD-MM-YYYY"}
            changeOnBlur
          />
        </Space>
      </Col>
      <Col span={12}>
        <Space size={4} direction="vertical">
          <Typography.Text> </Typography.Text>
          <TimePicker value={time} onChange={setTime} />
        </Space>
      </Col>

      <Col span={24}>
        <Divider style={{ margin: 0 }} />
      </Col>

      <Col span={24}>
        <Space size={8}>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={day}
              onChange={setDay}
              placeholder={""}
            />
          </Space>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={hour}
              onChange={setHour}
              placeholder={""}
            />
          </Space>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={minute}
              onChange={setMinute}
              placeholder={""}
            />
          </Space>
        </Space>
      </Col>

      <Col span={24}>
        <Row justify={"space-between"}>
          <Button type="primary" size="large" danger onClick={onReset}>
            
          </Button>
          <Button type="primary" ghost size="large" onClick={onSubstracts}>
            
          </Button>
          <Button type="primary" size="large" onClick={onAdd}>
            
          </Button>
        </Row>
      </Col>
      <Col span={24}>
        <Typography.Text strong>
          :{" "}
          {calcValue && (
            <Tag color="green" style={{ fontSize: 18 }}>
              {calcValue}
            </Tag>
          )}
        </Typography.Text>
      </Col>
    </Row>
  );
};
