import React, { useState } from "react";
import { Col, Row, Select, Typography } from "antd";
import "./App.css";
import { AddCmp } from "./add";
import { SummCmp } from "./summ";

type Mode = "summ" | "add";

const menu: Mode[] = ["add", "summ"];

function App() {
  const [viewMode, setViewMode] = useState<Mode>("add");

  return (
    <Row style={{ padding: 24 }} justify="center">
      <Col span={24}>
        <Row justify={"end"}>
          <Select
            value={viewMode}
            options={menu.map((m) => ({
              value: m,
              label: m === "summ" ? "" : "",
            }))}
            onChange={setViewMode}
          />
        </Row>
      </Col>
      <Col span={24}>
        <Row justify={"center"}>
          <Typography.Title level={3}> </Typography.Title>
        </Row>
      </Col>
      <Col>{viewMode === "add" ? <AddCmp /> : <SummCmp />}</Col>
    </Row>
  );
}

export default App;
