import {
  Button,
  Col,
  DatePicker,
  InputNumber,
  Row,
  Space,
  Tag,
  Typography,
} from "antd";
import React, { useState } from "react";
import dayjs from "dayjs";

export const SummCmp = () => {
  const [day1, setDay1] = useState<number | null>(null);
  const [hour1, setHour1] = useState<number | null>(null);
  const [minute1, setMinute1] = useState<number | null>(null);
  const [day2, setDay2] = useState<number | null>(null);
  const [hour2, setHour2] = useState<number | null>(null);
  const [minute2, setMinute2] = useState<number | null>(null);

  const [calcValue, setCalcValue] = useState<string[] | undefined>();

  const onCalc = () => {
    const d1 = dayjs.duration(day1 ?? 0, "d");
    const h1 = dayjs.duration(hour1 ?? 0, "h");
    const m1 = dayjs.duration(minute1 ?? 0, "m");
    const d2 = dayjs.duration(day2 ?? 0, "d");
    const h2 = dayjs.duration(hour2 ?? 0, "h");
    const m2 = dayjs.duration(minute2 ?? 0, "m");

    const res = d1.add(h1).add(m1).add(d2).add(h2).add(m2);
    const parsedValue = [
      `: ${res.months()}`,
      `: ${res.days()}`,
      `: ${res.hours()}`,
      `: ${res.minutes()}`,
    ];
    setCalcValue(parsedValue);
  };

  const onReset = () => {
    setDay1(null);
    setHour1(null);
    setMinute1(null);
    setDay2(null);
    setHour2(null);
    setMinute2(null);
    setCalcValue(undefined);
  };

  return (
    <Row gutter={[16, 16]} style={{ maxWidth: 333 }}>
      <Col span={24}>
        <Space size={8}>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={day1}
              onChange={setDay1}
              placeholder={""}
            />
          </Space>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={hour1}
              onChange={setHour1}
              placeholder={""}
            />
          </Space>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={minute1}
              onChange={setMinute1}
              placeholder={""}
            />
          </Space>
        </Space>
      </Col>
      <Col span={24}>
        <Row justify={"center"}>+</Row>
      </Col>

      <Col span={24}>
        <Space size={8}>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={day2}
              onChange={setDay2}
              placeholder={""}
            />
          </Space>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={hour2}
              onChange={setHour2}
              placeholder={""}
            />
          </Space>
          <Space size={4} direction="vertical">
            <Typography.Text>:</Typography.Text>
            <InputNumber
              style={{ width: "100%" }}
              value={minute2}
              onChange={setMinute2}
              placeholder={""}
            />
          </Space>
        </Space>
      </Col>

      <Col span={24}>
        <Row justify={"space-between"}>
          <Button type="primary" size="large" danger onClick={onReset}>
            
          </Button>
          <Button type="primary" size="large" onClick={onCalc}>
            
          </Button>
        </Row>
      </Col>
      <Col span={24}>
        <Space size={8} align="start">
          <Typography.Text strong>:</Typography.Text>
          {calcValue && (
            <Space direction="vertical">
              {calcValue.map((i, idx) => (
                <Tag key={idx} color="green" style={{ fontSize: 18 }}>
                  {i}
                </Tag>
              ))}
            </Space>
          )}
        </Space>
      </Col>
    </Row>
  );
};
